import React, { useState, useEffect } from 'react';

import ZuzenekoaCard from './ZuzenekoaCard';
import { Container, Menu, Icon, Segment, Grid, Card, Image, Dropdown, Label } from 'semantic-ui-react';
import { GetZuzenekoak } from './api.js';
import twitzlariak from './twitzlariak.json';


const Zuzenean = ((props) => {
    const [zuzenekoak, setZuzenekoak] = useState([])
    const twitzlariList = twitzlariak.twitzlariak.map((twitzlaria)=>{return twitzlaria})
    const getZuzenekoak = async() => setZuzenekoak(await GetZuzenekoak(twitzlariList));
    useEffect(()=>{
       getZuzenekoak();
    },[])
    return (
        <Container style={{ margin: 20 }}>
            <Segment>
                <Card.Group>
                    {zuzenekoak.length > 0 ? (
                        zuzenekoak.map((erabiltzailea, index) => (
                            <ZuzenekoaCard erabiltzailea={erabiltzailea} />
                        ))
                    ) : (
                        <ZuzenekoaCard />
                    )}
                </Card.Group>
            </Segment>
        </Container>
    )

})

export default Zuzenean;